___CSS_LOADER_EXPORT___.push([module.id, `.tile {
  perspective: 1000px;
  width: 200px;
  height: 200px;
  margin: 5px;
}

.tile-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.tile:hover .tile-inner {
  transform: rotateY(180deg);
  cursor: pointer;
}

.tile.reverse:hover .tile-inner {
  transform: rotateY(-180deg);
  cursor: pointer;
}

.tile-front,
.tile-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  border: 3px solid #ccc;
}

.tile-front {
  background-size: cover;
  background-position: center;
}

.tile-back {
  /* background-color: #fff; */
  transform: rotateY(180deg);
}

.tile.reverse .tile-back {
  transform: rotateY(-180deg);
}

@keyframes tileHover {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(180deg);
  }
}

@keyframes tileHoverReverse {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

.tile.scroll-animate .tile-inner {
  animation: tileHover 0.6s forwards;
}

.tile.scroll-animate-reverse .tile-inner {
  animation: tileHoverReverse 0.6s forwards;
}
